hr {
  border: 1px double rgba(255, 255, 255, 0.45);
}

.divider {
  display: none;
  border-top: solid 1px rgba(255, 255, 255, 0.45);
  border-bottom: solid 1px rgba(255, 255, 255, 0.45);
  width: 100%;
  height: 2px;
  margin: 15px 0;
}

.current {
  font-size: 18px;
  padding: 18px;
  color: white;
  backdrop-filter: blur(30px) saturate(200%);
  -webkit-backdrop-filter: blur(10px) saturate(200%);
  border-radius: 12px;
  /* background-color: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(209, 213, 219, 0.3); */

  /* background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(209, 213, 219, 0.15); */
}

.header {
  font-size: 1.1em;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

.header > svg {
  height: 20px;
  cursor: pointer;
}

.main {
  margin: 30px 30px 0;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.left {
  text-align: left;
}

.left span {
  display: block;
  margin-top: 15px;
}

.temperature {
  display: flex;
}

.minmax {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.minmax > div {
  display: flex;
  align-items: center;
  height: 30px;
}

.minmax > div > svg {
  width: 50px;
}

.temperature > p {
  font-size: 5em;
  font-weight: 800;
  font-family: "Stick No Bills", sans-serif;

  line-height: 70px;
  height: 72px;
}

.weatherDescription,
.suntime {
  height: 45px;
  text-transform: capitalize;
  border-top: 1px solid rgba(255, 255, 255, 0.45);
  display: flex;
  align-items: center;
  margin-top: 8px;
  padding-top: 8px;
  font-size: 0.9em;
}

.suntime {
  padding-top: 8;
}

.weatherDescription > svg {
  width: 70px;
}

.right {
  text-align: right;
}

.time {
  font-size: 4em;
  font-weight: 800;
  font-family: "Stick No Bills", sans-serif;

  line-height: 58px;
  height: 54px;
}

.time > span {
  font-size: 0.5em;
  font-weight: 300;
}

.date,
.location,
.feels {
  color: #ddd;
  font-size: 0.8em;
}

.suntime > svg {
  width: 70px;
}

.suntime > .time {
  font-size: unset;
  line-height: 70px;
  font-family: unset;
  font-weight: unset;
}

@media only screen and (max-width: 768px) {
  .current {
    font-size: 16px;
  }
  .temperature > p {
    height: 70px;
  }
}
@media only screen and (max-width: 500px) {
  .main {
    flex-direction: column;
    align-items: center;
  }
  .divider {
    display: unset;
  }
  .right,
  .left {
    text-align: center;
  }
}
